.Toolbar {
    height: 80px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
    /*box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.11), 0 6px 20px 0 rgba(0, 0, 0, 0.10);*/
}

.Container {
    width: 100%;
    max-width: 1028px;
    height: 100%;
    padding: 0px 32px;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 80%;
    width: auto;
}

.LinksAndLogin {
    height: 100%;
    width: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: right;
        justify-content: right;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.Toolbar span {
    color: var(--text-dark);
}

@media (max-width: 1027px) {
    .DesktopOnly {
        display: none;
    }
}



