/*
 * Here we are using Mobile-first approach. First thing, we create classes for mobile devices
 * and then we adjust the rendering based on bigger screen sizes.
**/

.NavigationItem {
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a {
    color: black;
    text-decoration: none;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    font-size: 16px;
    padding: 8px;
    margin: 8px 0px;
    text-align: right;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    color: var(--logo-orange);
}

@media (min-width: 1027px) {
    .NavigationItem {
        /* To center the link vertically */
        display: -ms-flexbox;
        display: flex;
        height: 100%;
        width: auto;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavigationItem a {
        /* color: #edefef; */
        color: var(--text-dark);
        height: 100%;
        padding: 32px 0px;
        margin: 0px 16px;
    }
    
    .NavigationItem a:hover {
        background-color: transparent;
        color: var(--logo-orange);
    }

    .NavigationItem a:active,
    .NavigationItem a.active {
        background-color: transparent;
        color: var(--logo-orange);
        border-bottom: 2px solid var(--logo-orange);
    }
}


