.Box {
    margin-top: 8px;
    width: 100%;
    height: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.Stop {
    width: 72px;
    height: 72px;
    border-radius: 100%;
    background-color: white;
    border: 1px solid var(--logo-red);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 6px 5px rgba(204, 57, 57, 0.1);
            box-shadow: 0 2px 6px 5px rgba(204, 57, 57, 0.1);
    -webkit-transition: all 1s cubic-bezier(.25,.8,.25,1);
    -o-transition: all 1s cubic-bezier(.25,.8,.25,1);
    transition: all 1s cubic-bezier(.25,.8,.25,1);
}

.Stop:hover {
    border: 1.4px solid var(--logo-red);
    -webkit-box-shadow: 0 2px 6px 5px rgba(204, 57, 57, 0.3);
            box-shadow: 0 2px 6px 5px rgba(204, 57, 57, 0.3);
}


