.Card {
    width: 100%;
    max-width: 500px;
    height: 400px;
    margin: auto;
    margin-bottom: 48px;
    border-radius: 8px;
    -webkit-box-shadow: 0 2px 20px 6px rgba(125, 125, 125, 0.1);
            box-shadow: 0 2px 20px 6px rgba(125, 125, 125, 0.1);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 24px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.Card h3 {
    text-align: center;
    font-size: 15px;
    font-weight: normal;
    color: var(--text-darker);
    line-height: 1.63;
    margin-top: 24px;
}

.Description {
    width: 100%;
    height: 350px;
