    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}

.Image {
    width: 112px;
    height: 112px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.Image img {
    width: 100%;
    height: 100%;
}

.Card button {
    height: 40px;
    width: 160px;
    padding: 0px 16px;
    border: none;
    color: var(--blue);
    background-color: white;
    border: 2px solid var(--blue);
    font-size: 15px;
    font-weight: bold;
    border-radius: 24px;
    -webkit-transition: all 0.4s cubic-bezier(.25,.8,.25,1);
    -o-transition: all 0.4s cubic-bezier(.25,.8,.25,1);
    transition: all 0.4s cubic-bezier(.25,.8,.25,1);
}

.Card button:hover {
    background-color: var(--blue);
    color: white;
}

@media (min-width: 750px) {
    .Card {
        max-width: 450px;
        margin: 0;
